.social__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 25px);
  justify-content: end;
  justify-items: end;
  gap: 11px;

  padding: 0;
  margin: 0;
  list-style-type: none;

  @include vp-767 {
    justify-content: start;
    justify-items: start;
  }
}

.social__link {
  display: inline-flex;
  width: 25px;
  height: 25px;
  align-items: center;
  justify-content: center;
  vertical-align: middle;

  background-color: $color-default-white;
  border-radius: 50%;
  color: inherit;
  transition: transform $trans-default;

  @include hover-focus {
    transform: scale(1.3);
  }

  &:focus {
    outline: none;
  }

  &:active {
    opacity: 0.4;
  }
}
