.promo__inner {
  position: relative;
  display: flex;
  padding-top: 119px;
  padding-bottom: 106px;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    flex-direction: column;
    padding-top: 70px;
    padding-bottom: 64px;
  }

  &::after {
    flex: 1 1 660px;

    background-image: url("../img/bg/promo-bg-desktop.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    content: "";

    @include vp-767 {
      flex: auto;
      width: 100%;
      padding-top: 100.69%;
      background-image: url("../img/bg/promo-bg-mobile.jpg");
    }

    @include retina {
      @media (max-width: ($vp-768 - 1px)) {
        background-image: url("../img/bg/promo-bg-mobile@2x.jpg");
      }

      @media (min-width: $vp-768) {
        background-image: url("../img/bg/promo-bg-desktop@2x.jpg");
      }
    }
  }
}

.promo__info {
  flex: 1 1 500px;

  @include vp-767 {
    flex: auto;
    width: 100%;
  }
}

.promo__info-content {
  min-height: 500px;

  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: repeat(3, auto) 1fr;
  align-content: center;

  padding: 83px 76px 86px 77px;

  background-color: $color-torch-red;
  color: $color-default-white;

  @include vp-1199 {
    padding: 45px 45px 52px;
    min-height: 292px;
  }

  @include vp-767 {
    position: relative;
    padding: 45px 44px 51px;
  }
}

.promo__info h2 {
  grid-column: 1 / 2;
  grid-row: 1 / 3;

  margin: 2px 58px 27px 14px;

  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 82px;
  line-height: 96%;
  letter-spacing: 0.03em;
  text-transform: uppercase;

  @include vp-1199 {
    margin: 5px 34px 10px 7px;

    font-size: 48px;
  }
}

.promo__info p {
  grid-column: 1 / 3;
  grid-row: 3 / 4;

  margin-top: 0;
  margin-bottom: 30px;

  font-size: 18px;

  @include vp-1199 {
    margin-bottom: 20px;

    font-size: 12px;
  }
}

.promo__options-list {
  grid-column: 2 / 3;
  grid-row: 1 / 3;

  margin: 0;
  padding: 0;
  margin-bottom: 27px;

  display: flex;
  flex-direction: column;

  @include vp-1199 {
    margin-bottom: 3px;
  }
}

.promo__options-item {
  display: grid;
  grid-template-columns: min-content;

  margin-bottom: 30px;

  @include vp-1199 {
    margin-bottom: 0;
  }

  @include vp-767 {
    margin-bottom: 2px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.promo__option-name {
  margin-bottom: 8px;

  font-size: 18px;
  line-height: 20px;

  @include vp-1199 {
    margin-bottom: 0;

    font-size: 12px;
  }
}

.promo__option-value {
  margin: 0;
  padding-top: 4px;

  display: grid;
  grid-template-columns: min-content auto;
  grid-template-rows: 14px 14px;
  gap: 5px 2px;

  border-top: 1px solid $color-default-white;

  @include vp-1199 {
    gap: 0;
    padding-top: 3px;
  }

  @include vp-767 {
    row-gap: 0;
  }
}

.promo__option-value b {
  grid-column: 1 / 2;
  grid-row: 1 / 3;

  font-weight: 700;
  font-size: 40px;
  line-height: 1;

  @include vp-1199 {
    font-size: 24px;
  }
}

.promo__option-value span {
  margin-left: 5px;

  font-size: 14px;

  @include vp-1199 {
    font-size: 10px;
    line-height: 1;
  }
}

.promo__button {
  grid-column: 1 / -1;
  grid-row: 4 / 5;

  padding: 24px;
  margin-top: auto;

  font-weight: 700;

  transition: box-shadow $trans-default;

  @include vp-1199 {
    padding: 11.5px;

    font-size: 10px;
    line-height: 12px;

    border-width: 2px;
  }

  @include hover-focus {
    box-shadow: inset 0 0 70px 0 rgba($color: $color-default-white, $alpha: 0.5);
  }

  &:focus {
    outline: 0;
  }

  &:active {
    transform: scale(0.95);
  }
}
